import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import Img from '../assets/logo.png'
import ReactHTMLTableToExcel from 'react-html-table-to-excel'


export default class Search extends Component {
    constructor(props){
        super(props)
        this.state = {
            total:'',
            employee: this.props.location.employee,
            admin: this.props.location.admin
        }
        this.total = 0
    }

    componentDidMount(){
        var data = []
            for(var i = 0; i < this.props.location.customers.length; i++){
                data.push(this.props.location.customers[i])
            }
            for(var y = 0; y < data.length; y++){
                this.total += data[y].price
                console.log(this.total)
            }
            this.setState({total: this.total})
    }
    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                  <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/> TslNigeria</a>
                  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                      <span className="navbar-toggler-icon"></span>
                  </button>

                  <div className="collapse navbar-collapse" id="navbarSupportedContent">
                      <ul className="navbar-nav ml-auto">
                          <li className="nav-item">
                             <Link className='nav-link' to={{
                          pathname: "/dashboard",
                          state: {
                            employee: this.state.employee,
                            admin: this.state.admin
                          }
                        }} style={{color:'black'}}><strong>Dashboard</strong></Link>
                          </li>
                          <li className="nav-item">
                             <Link className='nav-link' to={{
                          pathname: "/receipts",
                          state: {
                            employee: this.state.employee,
                            admin: this.state.admin
                          }
                        }} style={{color:'black'}}><strong>View Receipts</strong></Link>
                          </li>
                      </ul>

                  </div>
              </nav>
                <div className='container' style={{textAlign:'center'}}><br/><br/>
            <h4 style={{color:'red'}} >*Do Not Refresh This Page*</h4><br/><br/>
                <table id='table'>
                    <tbody>
                    <tr>
                        <th>Date</th>
                        <th>Customer</th>
                        <th className='hide'>Phone Number</th>
                        <th>Service</th>
                        <th className='hide'>Units</th>
                        <th className='hide'>Employee</th>
                        <th className='hide'>Discount</th>
                        <th className='hide'>Department</th>
                        <th>Price Paid</th>
                        <th>Print</th>
                    </tr>
                {
                    this.props.location.customers.map(dat => {
                        return(
                            <tr key={dat._id}>
                                <td>{dat.date}</td>
                                <td>{dat.customer}</td>
                                <td className='hide'>{dat.phone}</td>
                                <td>{dat.session}</td>
                                <td className='hide'>{dat.copies}</td>
                                <td className='hide'>{dat.employee}</td>
                                <td className='hide'>{dat.discount + '%'}</td>
                                <td className='hide'>{dat.department}</td>
                                <td>{'₦' + dat.price}</td>
                                <td><Link to={{
                                        pathname: '/print',
                                        state: {
                                            session: dat.session,
                                            date: dat.date,
                                            price: dat.price,
                                            customer: dat.customer,
                                            id: dat._id,
                                            discount: dat.discount,
                                            aprice: dat.aprice,
                                            employee: dat.employee,
                                            phone: dat.phone,
                                            copies: dat.copies,
                                            department: dat.department,
                                            admin: this.state.admin
                                        }
                                        }}><button className='btn btn-danger' id='print'>Print</button>
                                </Link></td>
                            </tr> 
                         ) 
                    }).reverse()
                }
                <tr>
                    <td><strong>Total</strong></td>
                    <td>{this.props.location.customers.length}</td>
                    <td className='hide'></td>
                    <td></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td>{'₦' + this.state.total}</td>
                    <td><ReactHTMLTableToExcel
                    id="export"
                    className="btn btn-success btn-md"
                    table="table"
                    filename="Table"
                    sheet="tablexls"
                    buttonText="Export"/></td>
                </tr>
                </tbody>
                </table><br/><br/><br/>
            </div>
            </div>
            
        )
    }
}
