import React, { Component } from 'react'
import axios from 'axios'
import '../App.css'
import {Link, Redirect} from 'react-router-dom'
import Img from '../assets/logo.png'
import ReactHTMLTableToExcel from 'react-html-table-to-excel'


export default class Receipts extends Component {
    constructor(props){
        super(props)
        this.state = {
            search: '',
            service: '',
            date: '',
            price: '',
            customer: '',
            id: '',
            success: '',
            discount:'',
            phone: '',
            employee:'',
            aprice:'',
            total:0,
            sdate:'',
            sdate1:'',
            number:'',
            department:'',
            eprops: this.props.location.state.employee,
            admin: this.props.location.state.admin
        }
        this.changeSearch = this.changeSearch.bind(this)
        this.submit = this.submit.bind(this)
        this.changeDate = this.changeDate.bind(this)
        this.changeDate1 = this.changeDate1.bind(this)
        this.searchRange = this.searchRange.bind(this)
        this.changeNumber = this.changeNumber.bind(this)
        this.changeDepartment = this.changeDepartment.bind(this)
        this.data = []
        this.customers = []
    }

    async componentDidMount(){
        console.log(this.props)
        var data = []
        // var url = 'https://tslposapi.herokuapp.com/receipts'
        // var url2 = 'http://localhost:5000/receipts'
        var url3 = '/receipts'
        axios.get(url3)
        .then((res) => {
            for(var i = 0; i < res.data.length; i++){
                data.push(res.data[i])
                this.setState({data: res.data[i], total: this.state.total + res.data[i].price})
            }
        })
        this.data = data
    }

    submit(e){
        e.preventDefault()
        var data = []
        // var url = 'https://tslposapi.herokuapp.com/search'
        // var url2 = 'http://localhost:5000/search'
        var url3 = '/search'
            axios.post(url3, {department: this.state.department, search: this.state.search, sdate: this.state.sdate, number: this.state.number})
            .then((res) => {
                for(var i = 0; i < res.data.length; i++){
                    data.push(res.data[i])
                    this.customers.push(res.data[i])
                }
                console.log(res.data.customers)
                this.customers = res.data.customers
                this.setState({success: res.data.success})
                console.log(this.customers)
            })
            
        }

        searchRange(e){
            e.preventDefault()
            var data = []
            // var url = 'https://tslposapi.herokuapp.com/range'
            // var url2 = 'http://localhost:5000/range'
            var url3 = '/range'
                axios.post(url3, {sdate1: this.state.sdate1, sdate: this.state.sdate})
                .then((res) => {
                    for(var i = 0; i < res.data.length; i++){
                        data.push(res.data[i])
                        this.customers.push(res.data[i])
                    }
                    console.log(res.data.customers)
                    this.customers = res.data.customers
                    this.setState({success: res.data.success})
                    console.log(this.customers)
                })
                
            }

    changeSearch(event){
        this.setState({search: event.target.value})
    }

    changeDepartment(event){
        this.setState({department: event.target.value})
      }

    changeDate(event){
        this.setState({sdate: event.target.value})
    }

    changeDate1(event){
        this.setState({sdate1: event.target.value})
    }

    changeNumber(event){
        this.setState({number: event.target.value})
    }

    render(){
        return(
            <div>
                <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                  <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/> TslNigeria</a>
                  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                      <span className="navbar-toggler-icon"></span>
                  </button>

                  <div className="collapse navbar-collapse" id="navbarSupportedContent">
                      <ul className="navbar-nav ml-auto">
                          <li className="nav-item">
                             <Link className='nav-link' to={{
                          pathname: "/dashboard",
                          state: {
                            employee: this.state.eprops,
                            admin: this.state.admin
                          }
                        }} style={{color:'black'}}><strong>Dashboard</strong></Link>
                          </li>
                      </ul>

                  </div>
              </nav>
              <div className='container' >
                  <div className='row'>
                      <div className='col-sm-6'>
                      <div id='search'>
                          <h4>Search Receipt</h4>
                        <form onSubmit={this.submit}>
                                <select onChange = {this.changeDepartment} name="session" id="session" style={{width:'100%'}} className = 'form-control form-group'>
                                    <option value="">-- Choose Department --</option>
                                    <option value="TslMoments">TslMoments</option>
                                    <option value="TslBrands">TslBrands</option>
                                    <option value="TslDigital">TslDigital</option>
                                    <option value="TslMedia">TslMedia</option>
                                </select>
                                <input id='text' type = 'text'
                                placeholder = 'Search Customer or Employee'
                                onChange = {this.changeSearch}
                                value = {this.state.search}
                                className = 'form-control form-group'
                                />
                                <input id='text' type = 'date'
                                placeholder = 'Date'
                                onChange = {this.changeDate}
                                value = {this.state.sdate}
                                className = 'form-control form-group'
                                />
                                <input id='text' type = 'number'
                                placeholder = 'Phone Number'
                                onChange = {this.changeNumber}
                                value = {this.state.number}
                                className = 'form-control form-group'
                                />
                                <input 
                                type = 'submit'
                                className = 'btn btn-danger btn-block'
                                value = 'Search'
                                />
                        </form><br/><br/>
                    </div>
                      </div>
                      <div className='col-sm-6'>
                      <div id='search'>
                          <h4>Search Range </h4>
                        <form onSubmit={this.searchRange}>
                            From:<br/>
                                <input id='text' type = 'date'
                                placeholder = 'Date'
                                onChange = {this.changeDate1}
                                value = {this.state.sdate1}
                                className = 'form-control form-group'
                                />
                                To:<br/>
                                <input id='text' type = 'date'
                                placeholder = 'Date'
                                onChange = {this.changeDate}
                                value = {this.state.sdate}
                                className = 'form-control form-group'
                                />
                                <input 
                                type = 'submit'
                                className = 'btn btn-danger btn-block'
                                value = 'Search'
                                />
                        </form><br/><br/>
                    </div>
                      </div>
                  </div>
                
                
                <table id='table' className='container'>
                    <tbody>
                    <tr>
                        <th>Date</th>
                        <th>Customer</th>
                        <th className='hide'>Phone Number</th>
                        <th>Service</th>
                        <th className='hide'>Units</th>
                        <th className='hide'>Employee</th>
                        <th className='hide'>Discount</th>
                        <th className='hide'>Department</th>
                        <th>Price Paid</th>
                        <th className='print'>Print</th>
                    </tr>
                {
                    this.data.map(dat => {
                        return(
                            <tr key={dat._id}>
                                <td>{dat.date}</td>
                                <td>{dat.customer}</td>
                                <td className='hide'>{dat.phone}</td>
                                <td>{dat.session}</td>
                                <td className='hide'>{dat.copies}</td>
                                <td className='hide'>{dat.employee}</td>
                                <td className='hide'>{dat.discount + '%'}</td>
                                <td className='hide'>{dat.department}</td>
                                <td>{'₦' + dat.price}</td>
                                <td className='print'><Link to={{
                                        pathname: '/print',
                                        state: {
                                            session: dat.session,
                                            date: dat.date,
                                            price: dat.price,
                                            customer: dat.customer,
                                            id: dat._id,
                                            discount: dat.discount,
                                            aprice: dat.aprice,
                                            employee: dat.employee,
                                            phone: dat.phone,
                                            copies: dat.copies,
                                            department: dat.department,
                                            admin: this.state.admin
                                        }
                                        }}><button className='btn btn-danger' id='print'>Print</button>
                                </Link></td>
                            </tr> 
                         ) 
                    }).reverse()
                }
                <tr>
                    <td><strong>Total</strong></td>
                    <td>{this.data.length}</td>
                    <td className='hide'></td>
                    <td></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td className='hide'></td>
                    <td>{'₦' + this.state.total}</td>
                    <td><ReactHTMLTableToExcel
                    id="export"
                    className="btn btn-success btn-md"
                    table="table"
                    filename="Table"
                    sheet="tablexls"
                    buttonText="Export"/></td>
                </tr>
                </tbody>
                </table><br/><br/><br/>
                {
                    this.state.success === true ? <Redirect to={{pathname:'/search', customers: this.customers, employee: this.state.eprops, admin: this.state.admin}} /> : null
                }
            </div>
            </div>
            
        )
    }
}
