import React, { Component } from 'react'
import {Link, Redirect} from 'react-router-dom'
import axios from 'axios'
import Img from '../assets/logo.png'


export default class Dashboard extends Component {
    constructor(props){
        super(props)
        this.state = {
            service: '',
            price: '',
            date: '',
            customer: '',
            success: '',
            logout: '',
            id: '',
            discount:'',
            phone: '',
            // employee: '',
            aprice: '',
            copies:'',
            // department:'',
            admin: this.props.location.state.admin
        }
        this.submit = this.submit.bind(this)
        this.logout = this.logout.bind(this)
        this.changeService = this.changeService.bind(this)
        this.changePrice = this.changePrice.bind(this)
        this.changeCustomer = this.changeCustomer.bind(this)
        this.changeDiscount = this.changeDiscount.bind(this)
        this.changePhone = this.changePhone.bind(this)
        // this.changeEmployee = this.changeEmployee.bind(this)
        // this.changeDepartment = this.changeDepartment.bind(this)
    }

    changePrice(event){
        this.setState({price: event.target.value})
      }

      changeCustomer(event){
        this.setState({customer: event.target.value})
      }

      changeDiscount(event){
        this.setState({discount: event.target.value})
      }

      changeService(event){
        this.setState({service: event.target.value})
      }

      changePhone(event){
        this.setState({phone: event.target.value})
      }

      // changeEmployee(event){
      //   this.setState({employee: event.target.value})
      // }

      // changeDepartment(event){
      //   this.setState({department: event.target.value})
      // }

    submit(event){
        event.preventDefault()
        const details = {
          service: this.state.service,
          customer: this.state.customer,
          discount: this.state.discount,
          phone: this.state.phone,
          employee: this.props.location.state.employee,
          department: 'TslMoments'
        }
        // var url = 'https://tslposapi.herokuapp.com/add'
        // var url2 = 'http://localhost:5000/add'
        var url3 = '/add'
        axios.post(url3, details)
        .then((res) => {this.setState({success: true, department: res.data.department, copies: res.data.copies, aprice: res.data.aprice, employee: res.data.employee, phone: res.data.phone, discount: res.data.discount, id: res.data._id, date: res.data.date, price: res.data.price, service: res.data.session}); console.log(res.data)})
        .catch(error => this.setState({success: false}))
    }

    logout(){
        // var url = 'https://tslposapi.herokuapp.com/logout'
        // var url2 = 'http://localhost:5000/logout'
        var url3 = '/logout'
        axios.get(url3)
        .then(res => this.setState({logout: res.data}))
        .catch(error => this.setState({logout: false}))
    }

    

    render() {
      if(this.state.admin === 'Super Admin'){
        return (
          <div>
            <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/> TslNigeria</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/receipts',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>View Receipts</strong></Link>
                        </li>
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/custom',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Custom Receipt</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className='nav-link' to='' onClick={this.logout}><button className='btn btn-danger' style={{borderRadius:'30px'}}>Log Out</button></Link>
                        </li>
                    </ul>

                </div>
            </nav>
              <div className='container' style={{marginTop: '10%'}}>
                  <h2 style={{textAlign:'center'}}>Initialize Receipt</h2><br/>
                  <div className='form-div'>
                  <span style={{color:'red'}}></span>
                      <form onSubmit={this.submit}>
                      Session:
                      <select required onChange = {this.changeService} name="session" id="session" style={{width:'100%', borderRadius:'30px'}} className = 'form-control form-group'>
                          <option value="">-- Choose Session --</option>
                            <option value="30mins">30 Mins. session</option>
                            <option value="1hour">1 Hour session</option>
                            <option value="children 30mins">Children shoot from 6months - 5years, 30Mins.</option>
                            <option value="children 1hour">Children shoot from 6months - 5years, 1Hour</option>
                            <option value="group 30mins">Group photo with family or friends, 30Mins.</option>
                            <option value="group 1hour">Group photo with family or friends, 1Hour</option>
                            <option value="product 30mins">Product shoot, 30Mins.</option>
                            <option value="product 1hour">Product shoot, 1Hour</option>
                            <option value="home 30mins">Photo session (Home service), 30Mins.</option>
                            <option value="home 1hour">Photo session (Home service), 1Hour</option>
                            <option value="outdoor 1hour">Photo session (Outdoor), 1Hour</option>
                            <option value="outdoor 30mins">Photo session (Outdoor), 30Mins.</option>
                            <option value="artist">Artist photoshoot @ the studio, 1Hour</option>
                      </select>
                      {/* Department:
                      <select required onChange = {this.changeDepartment} name="session" id="session" style={{width:'100%', borderRadius:'30px'}} className = 'form-control form-group'>
                          <option value="">-- Choose Department --</option>
                            <option value="TslMoments">TslMoments</option>
                            <option value="TslBrands">TslBrands</option>
                            <option value="TslDigital">TslDigital</option>
                            <option value="TslMedia">TslMedia</option>
                      </select> */}
                       Customer:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Customer'
                      onChange = {this.changeCustomer}
                      value = {this.state.customer}
                      className = 'form-control form-group'
                      />
                      Phone Number:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Phone Number'
                      onChange = {this.changePhone}
                      value = {this.state.phone}
                      className = 'form-control form-group'
                      />
                      {/* Employee:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Employee'
                      onChange = {this.changeEmployee}
                      value = {this.state.employee}
                      className = 'form-control form-group'
                      /> */}
                      Discount:
                      <input style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Discount'
                      onChange = {this.changeDiscount}
                      value = {this.state.discount}
                      className = 'form-control form-group'
                      />
                      <input 
                      type = 'submit'
                      className = 'btn btn-danger btn-block'
                      value = 'submit'
                      />
                      </form><br/>
                      <div style={{textAlign:'center'}}>
                      </div>
                  </div>
              </div>
              {
               this.state.logout === true ? <Redirect to='/'/> : null
              }
              {
               this.state.success === true ? <Redirect to={{pathname:'/receipt', state:{admin: this.state.admin, copies: this.state.copies, department: this.state.department, aprice: this.state.aprice, phone: this.state.phone, employee: this.state.employee, discount: this.state.discount, service: this.state.service, price: '₦' + this.state.price, customer: this.state.customer, id: this.state.id, date: this.state.date}}}/> : null
              }
          </div>
      )
      }else if(this.state.admin === 'Regular Admin'){
        return (
          <div>
            <nav className="navbar navbar-expand-md navbar-light bg-light" style={{textAlign:'center'}}>
                <a className="navbar-brand" href='/'><img src={Img} alt='' id="navimg" style={{width:'6rem', height:'3rem'}}/> TslNigeria</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item">
                           <Link className='nav-link' style={{color:'black'}} to={{
                                      pathname: '/custom',
                                      state: {
                                          employee: this.props.location.state.employee,
                                          admin: this.state.admin
                                      }
                                      }}><strong>Custom Receipt</strong></Link>
                        </li>
                        <li className="nav-item">
                            <Link className='nav-link' to='' onClick={this.logout}><button className='btn btn-danger' style={{borderRadius:'30px'}}>Log Out</button></Link>
                        </li>
                    </ul>

                </div>
            </nav>
              <div className='container' style={{marginTop: '10%'}}>
                  <h2 style={{textAlign:'center'}}>Initialize Receipt</h2><br/>
                  <div className='form-div'>
                  <span style={{color:'red'}}></span>
                      <form onSubmit={this.submit}>
                      Session:
                      <select required onChange = {this.changeService} name="session" id="session" style={{width:'100%', borderRadius:'30px'}} className = 'form-control form-group'>
                          <option value="">-- Choose Session --</option>
                            <option value="30mins">30 Mins. session</option>
                            <option value="1hour">1 Hour session</option>
                            <option value="children 30mins">Children shoot from 6months - 5years, 30Mins.</option>
                            <option value="children 1hour">Children shoot from 6months - 5years, 1Hour</option>
                            <option value="group 30mins">Group photo with family or friends, 30Mins.</option>
                            <option value="group 1hour">Group photo with family or friends, 1Hour</option>
                            <option value="product 30mins">Product shoot, 30Mins.</option>
                            <option value="product 1hour">Product shoot, 1Hour</option>
                            <option value="home 30mins">Photo session (Home service), 30Mins.</option>
                            <option value="home 1hour">Photo session (Home service), 1Hour</option>
                            <option value="outdoor 1hour">Photo session (Outdoor), 1Hour</option>
                            <option value="outdoor 30mins">Photo session (Outdoor), 30Mins.</option>
                            <option value="artist">Artist photoshoot @ the studio, 1Hour</option>
                      </select>
                      {/* Department:
                      <select required onChange = {this.changeDepartment} name="session" id="session" style={{width:'100%', borderRadius:'30px'}} className = 'form-control form-group'>
                          <option value="">-- Choose Department --</option>
                            <option value="TslMoments">TslMoments</option>
                            <option value="TslBrands">TslBrands</option>
                            <option value="TslDigital">TslDigital</option>
                            <option value="TslMedia">TslMedia</option>
                      </select> */}
                       Customer:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Customer'
                      onChange = {this.changeCustomer}
                      value = {this.state.customer}
                      className = 'form-control form-group'
                      />
                      Phone Number:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Phone Number'
                      onChange = {this.changePhone}
                      value = {this.state.phone}
                      className = 'form-control form-group'
                      />
                      {/* Employee:
                      <input required style={{width:'100%', borderRadius:'30px'}} type = 'text'
                      placeholder = 'Employee'
                      onChange = {this.changeEmployee}
                      value = {this.state.employee}
                      className = 'form-control form-group'
                      /> */}
                      Discount:
                      <input style={{width:'100%', borderRadius:'30px'}} type = 'number'
                      placeholder = 'Discount'
                      onChange = {this.changeDiscount}
                      value = {this.state.discount}
                      className = 'form-control form-group'
                      />
                      <input 
                      type = 'submit'
                      className = 'btn btn-danger btn-block'
                      value = 'submit'
                      />
                      </form><br/>
                      <div style={{textAlign:'center'}}>
                      </div>
                  </div>
              </div>
              {
               this.state.logout === true ? <Redirect to='/'/> : null
              }
              {
               this.state.success === true ? <Redirect to={{pathname:'/receipt', state:{admin: this.state.admin, copies: this.state.copies, department: this.state.department, aprice: this.state.aprice, phone: this.state.phone, employee: this.state.employee, discount: this.state.discount, service: this.state.service, price: '₦' + this.state.price, customer: this.state.customer, id: this.state.id, date: this.state.date}}}/> : null
              }
          </div>
      )
      }else{
        return(
          <Redirect to='/'/>
        )
        
      }
        
    }
}
