import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.js'
import axios from 'axios'
import './App.css'
import {BrowserRouter as Router, Route, Link, Redirect} from 'react-router-dom'
import Login from './components/Login'
import Dashboard from './components/Dashboard'
import Receipt from './components/Receipt'
import Receipts from './components/Receipts'
import Print from './components/Print'
import Search from './components/Search'
import Custom from './components/Custom'



export default class App extends Component {
  constructor(){
    super()
    this.state = {
      username: '',
      password: '',
      success: '',
      message: '',
      admin: ''
    }
    this.changeUsername = this.changeUsername.bind(this)
    this.changePassword = this.changePassword.bind(this)
    this.submit = this.submit.bind(this)
    this.changeType = this.changeType.bind(this)
  }

  changeUsername(event){
    this.setState({username: event.target.value})
  }

  changePassword(event){
    this.setState({password: event.target.value})
  }

  changeType(event){
    this.setState({type: event.target.value})
  }
  submit(event){
    event.preventDefault()
    const details = {
      username: this.state.username,
      password: this.state.password,
      type: this.state.type
    }
    this.setState({message: '*Loading*'})
    // var url = 'https://tslposapi.herokuapp.com/signup'
    // var url2 = 'http://localhost:5000/signup'
    var url3 = '/signup'
    axios.post(url3, details)
    .then(res => {console.log(res.data); this.setState({success: true, admin: res.data})})
    .catch(error => console.log(error))
  }  

  render() {
    return (
      <Router>
        <Route path='/signup' render={props=>(
          <div>
            <div className='container' style={{marginTop: '10%'}}>
              <h2 style={{textAlign:'center'}}>Sign Up</h2>
              <div className='form-div'>
              <span style={{color:'red'}}>{this.state.message}</span>
                <form onSubmit={this.submit}>
                  Username:
                  <input required type = 'text'
                  placeholder = 'Username'
                  onChange = {this.changeUsername}
                  value = {this.state.username}
                  className = 'form-control form-group'
                  />
                  Password:
                  <input required type = 'password'
                  placeholder = 'Password'
                  onChange = {this.changePassword}
                  value = {this.state.password}
                  className = 'form-control form-group'
                  />
                  Account Type:
                        <select required onChange = {this.changeType} name="type" id="session" style={{width:'100%'}} className = 'form-control form-group'>
                            <option value="">-- Choose Account Type --</option>
                              <option value="Super Admin">Super Admin</option>
                              <option value="Regular Admin">Regular Admin</option>
                        </select>
                  <input 
                  type = 'submit'
                  className = 'btn btn-danger btn-block'
                  value = 'submit'
                  />
                </form><br/>
                <div style={{textAlign:'center'}}><Link style={{color:'red'}} to='/'>*Login*</Link></div>
                {
                 this.state.success === true ? <Redirect to={{
                  pathname: "/dashboard",
                  state: {
                    employee: this.state.username,
                    admin: this.state.admin
                  }
                }}/> : null
                }
              </div>
            </div>
         </div>
        )} />
        <Route exact path = '/' component = {Login} />
        <Route path = '/dashboard' component = {Dashboard} />
        <Route path = '/receipts' component = {Receipts} />
        <Route path = '/receipt' component = {Receipt} />
        <Route path = '/print' component = {Print} />
        <Route path = '/search' component = {Search} />
        <Route path = '/custom' component = {Custom} />
      </Router>
    )
  }
}
