import React, { Component } from 'react'
import axios from 'axios'
import {Redirect, Link} from 'react-router-dom'

export default class Login extends Component {
    constructor(){
        super()
        this.state = {
          username: '',
          password: '',
          success: '',
          message: '',
          admin: ''
        }
        this.changeUsername = this.changeUsername.bind(this)
        this.changePassword = this.changePassword.bind(this)
        this.submit = this.submit.bind(this)
      }

    changeUsername(event){
        this.setState({username: event.target.value})
      }
    
      changePassword(event){
        this.setState({password: event.target.value})
      }
    
      submit(event){
        event.preventDefault()
        const details = {
          username: this.state.username,
          password: this.state.password
        }
        this.setState({message: '*Loading*'})
        // var url = 'https://tslposapi.herokuapp.com/login'
        // var url2 = 'http://localhost:5000/login'
        var url3 = '/login'
        axios.post(url3, details)
        .then((res) => {this.setState({admin: res.data}); console.log(res.data);
        if(this.state.admin === false){
          this.setState({message:'Invalid Username or Password', success: ''})
        }
      })
        .catch(error => this.setState({success: false, message:'Invalid Username or Password'}))
      }

    render() {
        return (
            <div>
                <div className='container' style={{marginTop: '10%'}}>
                    <h2 style={{textAlign:'center'}}>Log In</h2>
                    <div className='form-div'>
                    <span style={{color:'red'}}>{this.state.message}</span>
                        <form onSubmit={this.submit}>
                        Username:
                        <input required type = 'text'
                        placeholder = 'Username'
                        onChange = {this.changeUsername}
                        value = {this.state.username}
                        className = 'form-control form-group'
                        />
                        Password:
                        <input required type = 'password'
                        placeholder = 'Password'
                        onChange = {this.changePassword}
                        value = {this.state.password}
                        className = 'form-control form-group'
                        />
                        <input 
                        type = 'submit'
                        className = 'btn btn-danger btn-block'
                        value = 'submit'
                        />
                        </form><br/>
                        <Link to='/signup' style={{color:'red'}}>*Initialise Account*</Link>
                    </div>
                </div>
                {
                        this.state.admin ? <Redirect to={{
                          pathname: "/dashboard",
                          state: {
                            employee: this.state.username,
                            admin: this.state.admin
                          }
                        }} /> : null
                } 
            </div>
        )
    }
}
